import wrap from '@/components/wrap';
import Toast from '../../wxcomponents/vant/toast/toast';
export default {
  data() {
    return {
      show: {
        middle: false,
        top: false,
        bottom: false,
        right: false,
        right2: false,
      },
    };
  },
  methods: {
    show() {
      Toast('我是弹窗哈哈');
    },
    toggle(type) {
      console.log(type);
      this.show[type] = !this.show[type];
    },
    togglePopup() {
      this.toggle('middle');
    },

    toggleRightPopup() {
      this.toggle('right');
    },

    toggleRightPopup2() {
      this.toggle('right2');
    },

    toggleBottomPopup() {
      this.toggle('bottom');
    },

    toggleTopPopup() {
      this.toggle('top');
      setTimeout(() => {
        this.toggle('top');
      }, 2000);
    },
  },
  components: {
    wrap,
  },
};
